import {React, createContext, useReducer,useEffect} from 'react'
import './Cart.css'

import ContextCart from './ContextCart';
import { Products } from './Products';
import {reducer }from './reducer'

export const CartContext = createContext()

const initialState = {
    item: Products,
    totalAmount: 0,
    totalItems:0,

}

const Cart = () => {
    const [state, dispatch] = useReducer(reducer, initialState)

    const RemoveItem = (id) =>{
    return dispatch({
        type: "REMOVE_ITEM",
        payLoad: id
    })
    }

    const clearCart = () =>{
        return dispatch ( {
            type: "CLEAR_ALL"
        })
    }
    const quantityINC = (id) =>{
        return dispatch(
            {type: "INCREMENT", payLoad : id}
        )
    }
    const quantityDEC = (id) =>{
        return dispatch(
            {type: "DECREMENT", payLoad : id}
        )
    }
    useEffect(() => {
        dispatch ({ type: "GET_TOTAL"})
    
                
    }, [state.item]);

    return (
       <div className="container-fluid cart_container">
       <CartContext.Provider value={{...state, RemoveItem, clearCart, quantityINC,quantityDEC}}>
             <ContextCart/>
       </CartContext.Provider>
       
         
       </div>
    )
}

export default Cart
