import { React, useContext } from "react";
import { Scrollbars } from "react-custom-scrollbars-2";
import { CartContext } from "./Cart";
import { Items } from "./Items";

const ContextCart = () => {
  // const [item, setItem] = useState(Products)
  const { item, clearCart, totalItems, totalAmount } = useContext(CartContext);

  return (
    <>
      <header>

        <div className="cart-total">
          <h3>
            Cart Total: <span> {totalAmount} rs</span>
          </h3>
          <button>check out</button>
          <button className="btn-clear" onClick={clearCart}>
            Clear Cart
          </button>
        </div>
        <div className="cart-icon">

          <img src="../Images/cart.png" alt="" />
          <p>{totalItems}</p>
        </div>
      </header>

      <section className="main-cart-section">
        <p className="total-items">
          You have <span className="total-items-count">{totalItems}</span> items
          in shopping cart
        </p>

        <div className="cart-items">
          <div className="cart-items-container container">
            <Scrollbars>
              {item.map((currentElement) => {
                return <Items key={currentElement.id} {...currentElement} />;
              })}
            </Scrollbars>
          </div>
        </div>
      </section>
    </>
  );
};

export default ContextCart;
