import {useContext,React} from 'react'
import { CartContext } from './Cart'

export const Items = ({title,description,price,img,quantity, id}) => {
    const {RemoveItem, quantityINC,quantityDEC } = useContext(CartContext)

    return (
        <>

        <div className="items-info">
                  <div className="product-img">
                      <img src={img} alt="" />
                  </div>
                  <div className="title">
                      <h2>{title}</h2>
                      <p>{description} </p>
                  </div>
                  <div className="add-minus-quantity">
                      <i className="fas fa-minus minus" onClick={()=> quantityDEC(id)} ></i>
                      <input type="text" name="" placeholder={quantity} id="" />
                      <i className="fas fa-plus add" onClick={() => quantityINC(id)} ></i>

                  </div>
                    <div className="price">
                        <h3>{price}</h3>
                    </div>
                    <div className="remove-item">
                        <i className="fas fa-trash-alt remove" onClick={()=>RemoveItem(id)}></i>
                    </div>
              </div>
              <hr />



            {/* <div className="items-info">
                  <div className="product-img">
                      <img src={img} alt="" />
                  </div>
                  <div className="title">
                      <h2>{title}</h2>
                      <p>{description} </p>
                  </div>
                  <div className="add-minus-quantity">
                      <i className="fas fa-minus minus" onClick={()=> quantityDEC(id)} ></i>
                      <input type="text" name="" placeholder={quantity} id="" />
                      <i className="fas fa-plus add" onClick={() => quantityINC(id)} ></i>

                  </div>
                    <div className="price">
                        <h3>{price}</h3>
                    </div>
                    <div className="remove-item">
                        <i className="fas fa-trash-alt remove" onClick={()=>RemoveItem(id)}></i>
                    </div>
              </div>
              <hr /> */}
        </>
    )
}
