
export const reducer = (state, action) => {
    if (action.type === "REMOVE_ITEM") {
        return {
            ...state,
            item: state.item.filter((currElement) => {
                return currElement.id !== action.payLoad
            })
        }
    }

    if (action.type === "CLEAR_ALL") {
        return { ...state, item: [] };
    }
    if (action.type === "INCREMENT") {
        let updatedCart = state.item.map((currElement) => {
            if (currElement.id === action.payLoad) {
                return { ...currElement, quantity: currElement.quantity + 1 }
            }
            return currElement;
        })

        return { ...state, item: updatedCart }
    }
    if (action.type === "DECREMENT") {
        let updatedCart = state.item.map((currElement) => {
            if (currElement.id === action.payLoad) {
                return { ...currElement, quantity: currElement.quantity - 1 }

            }
            return currElement;
        }).filter((currElem) => {
            return currElem.quantity !== 0;
        })

        return { ...state, item: updatedCart }
    }
    if(action.type === "GET_TOTAL"){
        let {totalItems, totalAmount} = state.item.reduce(
            (accum, curVal) => {
            let {price, quantity} = curVal;
            let updatedTotalAmount = price * quantity;
        accum.totalAmount += updatedTotalAmount;
            accum.totalItems += quantity;
            return accum;
        },
        
        {
            totalItems : 0,
            totalAmount: 0
        })
        return {...state, totalItems,totalAmount }
    }

    return state
}


